import { Contract } from "@ethersproject/contracts";
import erc20Abi from "../constants/abis/mintableErc20.json";
import { Web3Provider } from "@ethersproject/providers";
import { parseEther } from "@ethersproject/units";

export class PairedTokenService {
	public contract: Contract;
	public account: string;
	public tokenAddress: string;

	public constructor(library: Web3Provider, account: string, tokenAddress: string) {
		const signer = library.getSigner(account).connectUnchecked();
		this.contract = new Contract(tokenAddress, erc20Abi, signer);
		this.account = account;
		this.tokenAddress = tokenAddress;
	}

	public async mint(amount: string) {
		return await this.contract.mint(parseEther(amount));
	}
}
