import React, { useState, useContext } from 'react';
import { useWeb3React } from '@web3-react/core';
import { ButtonPrimary, PendingContent } from '../../components/Button'
import { SemiTransparentCard } from '../Card'
import { TimeRiftService } from '../../services/TimeRiftService';
import TradeContext from '../../contexts/TradeContext';
import { TradeInfoService } from '../../services/TradeInfoService';

export const BalancePrices = () => {
	const { account, library } = useWeb3React();
	const [pending, setPending] = useState<boolean>(false);
	const { addTradeInfo } = useContext(TradeContext);

	const balancePrices = async () => {
		setPending(true)
		try {
			const txResponse = await new TimeRiftService(library, account!).balancePrices()
			if (txResponse) {
				await txResponse.wait();
				addTradeInfo(await new TradeInfoService(library, account!).getTradeInfo("Balance Prices"));
			}
		}
		catch (e) {
			console.log(e);
		}
		setPending(false)
	}

	return (
		<SemiTransparentCard>
			<ButtonPrimary disabled={pending} onClick={balancePrices}>
				{pending ? <PendingContent text={"Balancing..."} /> : "Balance Prices"}
			</ButtonPrimary>
		</SemiTransparentCard>
	)
}