import { createContext } from "react";
import { TradeInfo } from "../dtos/TradeInfo";

export interface TradeContextInterface {
	addTradeInfo: (trade: TradeInfo) => void;
	trades: TradeInfo[];
}

const TradeContext = createContext<TradeContextInterface>({
	addTradeInfo: (trade: TradeInfo) => { },
	trades: [],
});

export default TradeContext;
