import React, { useState, useContext } from 'react';
import { useWeb3React } from '@web3-react/core';
import styled from 'styled-components'
import { ButtonPrimaryGreen, PendingContent } from '../../components/Button'
import { SemiTransparentCard } from '../Card'
import CurrencyInput from '../CurrencyInput'
import useTokenBalance from '../../hooks/useTokenBalance';
import { PAIRED_ADDRESS, PAIRED_TICKER } from '../../constants';
import { getDisplayBalance, getFullDisplayBalance } from '../../utils/formatBalance';
import { RouterService } from '../../services/RouterService';
import TradeContext from '../../contexts/TradeContext';
import { TradeInfoService } from '../../services/TradeInfoService';

const Wrapper = styled.div`
    display: grid;
    grid-gap: 1em;`

export const Buy = () => {
	const { account, library } = useWeb3React();
	const [value, setValue] = useState<string>('')
	const [pending, setPending] = useState<boolean>(false)
	const balance = useTokenBalance(PAIRED_ADDRESS);
	const { addTradeInfo } = useContext(TradeContext)

	const buy = async () => {
		const amount = parseFloat(value)
		if (!Number.isNaN(amount) && amount > 0) {
			setPending(true)
			try {
				const txResponse = await new RouterService(library, account!).buy(value)
				if (txResponse) {
					await txResponse.wait()
					setValue('');
					addTradeInfo(await new TradeInfoService(library, account!).getTradeInfo("Buy"));
				}
			}
			catch (e) {
				console.log(e);
			}
			setPending(false)
		}
	}

	return (
		<SemiTransparentCard>
			<Wrapper>
				<CurrencyInput
					value={value.toString()}
					onUserInput={(val) => { setValue(val) }}
					onSubmit={buy}
					onMax={() => setValue(getFullDisplayBalance(balance))}
					showMaxButton={true}
					balance={getDisplayBalance(balance, 2)}
					ticker={PAIRED_TICKER}
					id="buyInput" />
				<ButtonPrimaryGreen disabled={pending} onClick={buy}>
					{pending ? <PendingContent text={"Buying..."} /> : "Buy"}
				</ButtonPrimaryGreen>
			</Wrapper>
		</SemiTransparentCard>
	)
} 