export class TradeInfo {
	public name: string;
	public trueFlowerPrice: string;
	public truePairedPrice: string;
	public poolFlowerPrice: string;
	public poolPairedPrice: string;
	public totalSupply: string;
	public circulatingSupply: string;

	constructor(name: string, trueFlowerPrice: string, truePairedPrice: string, poolFlowerPrice: string, poolPairedPrice: string, totalSupply: string, circulatingSupply: string) {
		this.name = name;
		this.trueFlowerPrice = trueFlowerPrice;
		this.truePairedPrice = truePairedPrice;
		this.poolFlowerPrice = poolFlowerPrice;
		this.poolPairedPrice = poolPairedPrice;
		this.totalSupply = totalSupply;
		this.circulatingSupply = circulatingSupply;
	}
}
