import React, { useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import styled from 'styled-components'
import { ButtonPrimary, PendingContent } from '../../components/Button'
import { SemiTransparentCard } from '../Card'
import CurrencyInput from '../CurrencyInput'
import useTokenBalance from '../../hooks/useTokenBalance';
import { PAIRED_ADDRESS, PAIRED_TICKER } from '../../constants';
import { PairedTokenService } from '../../services/PairedTokenService';
import { getDisplayBalance } from '../../utils/formatBalance';

const Wrapper = styled.div`
    display: grid;
    grid-gap: 1em;`

export const Mint = () => {
	const { account, library } = useWeb3React();
	const [value, setValue] = useState<string>('')
	const [pending, setPending] = useState<boolean>(false)
	const balance = useTokenBalance(PAIRED_ADDRESS);

	const mint = async () => {
		const amount = parseFloat(value)
		if (!Number.isNaN(amount) && amount > 0) {
			setPending(true)
			try {
				const txResponse = await new PairedTokenService(library, account!, PAIRED_ADDRESS).mint(value)
				if (txResponse) {
					await txResponse.wait()
					setValue('')
				}
			}
			catch (e) {
				console.log(e);
			}
			setPending(false)
		}
	}

	return (
		<SemiTransparentCard>
			<Wrapper>
				<CurrencyInput
					value={value.toString()}
					onUserInput={(val) => { setValue(val) }}
					onSubmit={mint}
					onMax={() => { }}
					showMaxButton={false}
					balance={getDisplayBalance(balance, 2)}
					ticker={PAIRED_TICKER}
					id="mintInput" />
				<ButtonPrimary disabled={pending} onClick={mint}>
					{pending ? <PendingContent text={"Minting..."} /> : "Mint BUSD"}
				</ButtonPrimary>
			</Wrapper>
		</SemiTransparentCard>
	)
} 