import React, { useContext } from 'react';
import styled from 'styled-components'
import { SemiTransparentCard } from '../Card'
import TradeContext from '../../contexts/TradeContext';

const Wrapper = styled.div`
    display: grid;
    grid-gap: 1em;`

const TradesWrapper = styled.div`
    display: grid;
    align-items: center;
    padding: 0 0.5em;
    font-size: 0.825rem;
    font-family: monospace;
	background-color: ${({ theme }) => theme.bg1};
	border-radius: 1em;
	height: 70em;
	padding: 1em;
	align-content: start;
	overflow-y: auto;
`

const TradeWrapper = styled.div`
    display: grid;
    grid-template-columns: 11em 1fr;
    grid-column-gap: 1em;   
`

const PlaceHodler = styled.div`
	height:1em`

export const TradingLog = () => {
	const { trades } = useContext(TradeContext);

	return (
		<SemiTransparentCard>
			<Wrapper>
				<div>TRADING LOG</div>
				<TradesWrapper>
					{trades?.map((x, i) => (
						<TradeWrapper key={i}>
							<div>
								{x.name}
							</div>
							<div />
							<div>
								Total Supply:
							</div>
							<div>
								{x.totalSupply}
							</div>
							<div>
								Circulating Supply:
							</div>
							<div>
								{x.circulatingSupply}
							</div>
							<div>
								Lotus Pool Price:
							</div>
							<div>
								{x.poolFlowerPrice}
							</div>
							<div>
								Lotus True Price:
							</div>
							<div>
								{x.trueFlowerPrice}
							</div>
							<PlaceHodler />
							<div />
						</TradeWrapper>))}
				</TradesWrapper>
			</Wrapper>
		</SemiTransparentCard>
	)
} 