import React, { useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import styled from 'styled-components'
import { ButtonPrimary, PendingContent } from '../../components/Button'
import { SemiTransparentCard } from '../Card'
import { FLOWER_ADDRESS, PAIRED_ADDRESS, ROUTER_ADDRESS } from '../../constants';
import { TokenService } from '../../services/TokenService';

const Wrapper = styled.div`
    display: grid;
    grid-gap: 1em;`

export const Approve = () => {
	const { account, library } = useWeb3React();

	const [flowerApproving, setFlowerApproving] = useState<boolean>(false)
	const [pairedApproving, setPairedApproving] = useState<boolean>(false)

	const pairedApprove = async () => {
		setPairedApproving(true);
		try {
			const txResponse = await new TokenService(library, account!, PAIRED_ADDRESS).approve(ROUTER_ADDRESS)
			if (txResponse) {
				await txResponse.wait();
			}
		}
		catch (e) {
			console.log(e);
		}
		setPairedApproving(false);
	}

	const flowerApprove = async () => {
		setFlowerApproving(true);
		try {
			const txResponse = await new TokenService(library, account!, FLOWER_ADDRESS).approve(ROUTER_ADDRESS)
			if (txResponse) {
				await txResponse.wait();
			}
		}
		catch (e) {
			console.log(e);
		}
		setFlowerApproving(false);
	}

	return (
		<SemiTransparentCard>
			<Wrapper>
				<ButtonPrimary disabled={pairedApproving} onClick={pairedApprove}>
					{pairedApproving ? <PendingContent text={"Approving..."} /> : "Max Approve BUSD"}
				</ButtonPrimary>
				<ButtonPrimary disabled={flowerApproving} onClick={flowerApprove}>
					{flowerApproving ? <PendingContent text={"Approving..."} /> : "Max Approve LOTUS"}
				</ButtonPrimary>
			</Wrapper>
		</SemiTransparentCard>
	)
} 