import { Contract } from "@ethersproject/contracts";
import routerAbi from "../constants/abis/router.json";
import { Web3Provider } from "@ethersproject/providers";
import { parseEther } from "@ethersproject/units";
import { FLOWER_ADDRESS, PAIRED_ADDRESS, ROUTER_ADDRESS } from "../constants";

export class RouterService {
	public contract: Contract;
	public account: string;

	public constructor(library: Web3Provider, account: string) {
		const signer = library.getSigner(account).connectUnchecked();
		this.contract = new Contract(ROUTER_ADDRESS, routerAbi, signer);
		this.account = account;
	}

	public async buy(amount: string) {
		return await this.contract.swapExactTokensForTokensSupportingFeeOnTransferTokens(parseEther(amount), 0, [PAIRED_ADDRESS, FLOWER_ADDRESS], this.account, 2e9);
	}

	public async sell(amount: string) {
		return await this.contract.swapExactTokensForTokensSupportingFeeOnTransferTokens(parseEther(amount), 0, [FLOWER_ADDRESS, PAIRED_ADDRESS], this.account, 2e9);
	}
}
