import { Contract } from "@ethersproject/contracts";
import flowerAbi from "../constants/abis/flower.json";
import poolAbi from "../constants/abis/pair.json";
import { Web3Provider } from "@ethersproject/providers";
import { FLOWER_ADDRESS, POOL_ADDRESS } from "../constants";
import { formatEther } from "@ethersproject/units";
import { TradeInfo } from "../dtos/TradeInfo";

export class TradeInfoService {
	public account: string;
	public flowerContract: Contract;
	public poolContract: Contract;

	public constructor(library: Web3Provider, account: string) {
		const signer = library.getSigner(account).connectUnchecked();
		this.flowerContract = new Contract(FLOWER_ADDRESS, flowerAbi, signer);
		this.poolContract = new Contract(POOL_ADDRESS, poolAbi, signer);
		this.account = account;
	}

	public async getTradeInfo(name: string) {
		const prices: any = await this.flowerContract.getPrices();
		const reserves: any = await this.poolContract.getReserves();
		const reserve0 = parseFloat(formatEther(reserves[0]));
		const reserve1 = parseFloat(formatEther(reserves[1]));
		const totalSupply = formatEther(await this.flowerContract.totalSupply());
		const poolBalance = formatEther(await this.flowerContract.balanceOf(POOL_ADDRESS));
		const circulatingSupply = parseFloat(totalSupply) - parseFloat(poolBalance);
		return new TradeInfo(name, formatEther(prices[1]), formatEther(prices[0]), (reserve1 / reserve0).toString(), (reserve0 / reserve1).toString(), parseFloat(totalSupply).toFixed(12), circulatingSupply.toFixed(12));
	}
}
